html, body {
    height: 100%;
}

body {
    margin: 0;
}

body {
    font-family: 'Open Sans', sans-serif;
    -ms-overflow-y: auto !important;
    font-size: 14px;
}

* {
    box-sizing: border-box;
}

#root {
    height: 100%;
}

a {
    text-decoration: none;
    cursor: pointer;
    color: text-blue;
}

a:hover {
    color: darken(text-blue, 20%);
}

.sample {
  width: 100%;
  height: 100%;
  background:#aeeeee;

  position: relative;
  padding-bottom: 80px;
}

.sample img {
  position: absolute;
}

.sample .ground {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 84px;

  background: #4aa62b;
}
